import React from "react"
import { graphql } from "gatsby"
import Layout from "../components/layout"
import Seo from "../components/seo"
import { ImprintContainer } from "../components/ImprintSection/ImprintElements"
import { Anchor } from "../components/MainElements"
import { useTranslation } from "gatsby-plugin-react-i18next"

const Imprint = () => {
  const { t } = useTranslation()

  return (
    <Layout>
      <Seo title={t("imprint:title")} />
      <ImprintContainer id="imprint">
        <h1>{t("imprint:title")}</h1>
        {t("imprint:textElements", { returnObjects: true }).map(
          (item, index) => {
            const Tag = item.type
            return item.id === "" ? (
              <Tag key={index}>{item.text}</Tag>
            ) : (
              <div key={index}>
                <Anchor id={item.id} />
                <Tag>{item.text}</Tag>
              </div>
            )
          }
        )}
      </ImprintContainer>
    </Layout>
  )
}

export default Imprint

export const query = graphql`
  query ($language: String!) {
    locales: allLocale(
      filter: {
        ns: { in: ["imprint", "navigation"] }
        language: { eq: $language }
      }
    ) {
      edges {
        node {
          ns
          data
          language
        }
      }
    }
  }
`
